




























export default {
    props: {
        data: {
            type: Object,
            default: function(e) {
                return {}
            }
        }
    },
    computed: {
        isImgRight() {
            return this.data.article_type === 2
        },
        isImgLeft() {
            return this.data.article_type === 1
        },
        showImg() {
            return this.data.image_list || this.data.image_url
        }
    },
    methods: {
        close(e) {
            this.$emit('close');
        },
        bindClick() {
            this.$emit('click');
        }
    }
}
